import React, { Component } from "react";
import "./Blogs_Content.css";
import BlogContentCard from "../../components/blogContentCard/BlogContentCard.js";
import { blogs_cont } from "../../portfolio";
import { Fade } from "react-reveal";

class BlogsContent extends Component {
  render() {
    const theme = this.props.theme;
    return (
      <div className="main" id="blogscontent">
        <div className="blogscontent-header-div">
          <Fade bottom duration={2000} distance="20px">
            <h1 className="blogscontent-header" style={{ color: theme.text }}>
              Articles Written
            </h1>
          </Fade>
        </div>
        <div className="blogscontent-body-div">
          {blogs_cont.blogs_cont.map((blogs_cont) => {
            return <BlogContentCard blogs_cont={blogs_cont} theme={theme} />;
          })}
        </div>
      </div>
    );
  }
}

export default BlogsContent;
