import React, { Component } from "react";
import Header from "../../components/header/Header";
import Footer from "../../components/footer/Footer";
import TopButton from "../../components/topButton/TopButton";
import Blogs_Content from "../../containers/blog_content/Blogs_Content";
import BlogsHighContent from "../../containers/blog_high_content/BlogsHighContent";
import Blogs from "../../containers/blog/Blogs";
import BlogImg from "./BlogImg";
import { blog_highlights } from "../../portfolio";
import "./BlogComponent.css";
import { Fade } from "react-reveal";

class Blog extends Component {
  render() {
    const theme = this.props.theme;
    return (
      <div className="blog-main">
        <Header theme={this.props.theme} />
        <div className="basic-blog">
          <Fade bottom duration={2000} distance="40px">
            <div className="heading-div">
              <div className="heading-img-div">
                {/* <img
									src={require("../../assests/images/education.svg")}
									alt=""
								/> */}
                <BlogImg theme={theme} />
              </div>
              <div className="heading-text-div">
                <h1 className="heading-text" style={{ color: theme.text }}>
                  Blog
                </h1>
                <h9 className="heading-sub-text" style={{ color: theme.text }}>
                  I write empowering blogs in data science and tech, fostering inclusivity for women to thrive. Let's break barriers, drive innovation, and shape the future. Together, we'll empower an inclusive tech landscape.
                </h9>
              </div>
            </div>
          </Fade>
          <Blogs_Content theme={this.props.theme} />
          {blog_highlights.blog_highlights.length > 0 ? (
            <BlogsHighContent theme={this.props.theme} />
          ) : null}
        </div>
        <Footer theme={this.props.theme} />
        <TopButton theme={this.props.theme} />
      </div>
    );
  }
}

export default Blog;
