import React, { Component } from "react";
import "./BlogsHighContent.css";
import { Fade } from "react-reveal";
import { blog_highlights } from "../../portfolio";
import BlogHighlightCard from "../../components/blogHighlightCard/BlogHighlightCard";

class BlogsHighContent extends Component {
  render() {
    const theme = this.props.theme;
    return (
      <div className="main" id="bloghigh">
        <div className="bloghigh-header-div">
          <Fade bottom duration={2000} distance="20px">
            <h1 className="bloghigh-header" style={{ color: theme.text }}>
              Featured Articles
            </h1>
          </Fade>
        </div>
        <div className="bloghigh-body-div">
          {blog_highlights.blog_highlights.map((cert) => {
            return <BlogHighlightCard blog_highlights={cert} theme={theme} />;
          })}
        </div>
      </div>
    );
  }
}

export default BlogsHighContent;
