import React, { Component } from "react";
import "./BlogHighlightCard.css";
import { Fade } from "react-reveal";

class BlogHighlightCard extends Component {
  render() {
    const blog_highlights = this.props.blog_highlights;
    const theme = this.props.theme;
    return (
      <Fade bottom duration={2000} distance="20px">
        <div className="bloghigh-card">
          <div className="content">
            <a
              href={blog_highlights.certificate_link}
              target="_blank"
              rel="noopener noreferrer"
            >
              <div className="content-overlay"></div>
              <div
                className="bloghigh-header"
                style={{ backgroundColor: blog_highlights.color_code }}
              >
                <img
                  className="logo_img"
                  src={require(`../../assests/images/${blog_highlights.logo_path}`)}
                  alt={blog_highlights.alt_name}
                />
              </div>
              <div className="content-details fadeIn-top">
                <h3 className="content-title" style={{ color: theme.body }}>
                  Explore it
                </h3>
              </div>
            </a>
          </div>
          <div className="bloghigh-body">
            <h2 className="bloghigh-body-title" style={{ color: theme.text }}>
              {blog_highlights.title}
            </h2>
            <h3
              className="bloghigh-body-subtitle"
              style={{ color: theme.secondaryText }}
            >
              {blog_highlights.subtitle}
            </h3>
          </div>
        </div>
      </Fade>
    );
  }
}

export default BlogHighlightCard;
